import { Link } from "react-router-dom";
import axios from "axios";
import logo from '../images/logo.png';
const Cookie = require('js-cookie');

export function RegisterPage() {

    const handleSubmit = async (event) => {
        const regex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;
        event.preventDefault();
        if (!event.target.password.value || !event.target.name.value || !event.target.email.value) {
            window.alert("All fields must be completed!");
        }
        else {
            if (event.target.password.value !== event.target.passwordRepeat.value) {
                window.alert("Passwords do not match!");
            }
            else if (event.target.password.value.match(regex) === null) {
                window.alert("Password must be at least 8 characters long, contain one number, one letter and one special character");
            }
            else {
                const newUser = {
                    name: event.target.name.value,
                    email: event.target.email.value,
                    password: event.target.password.value
                }
                try {
                    const success = await axios.post('/register', newUser);
                    if (success.data) {
                        Cookie.set('userName', success.data.name);
                        Cookie.set('userEmail', success.data.email);
                        Cookie.set('userID', success.data.id);
                        Cookie.set('userIsAdmin', success.data.isadmin);
                        Cookie.set('goal', 1);
                        window.location.href = '/regsisterSuccess';
                    }
                }
                catch(error) {window.alert('This user is already registered!');}
            }
        }
    };

    const handleMouseOver = () => {
        document.getElementById("popUp").style.display = "flex";
    }

    const handleMouseLeave = () => {
        document.getElementById("popUp").style.display = "none";
    }

    return (
      <div className="SidePage">
        <div className="HeaderSection">
        <div className="headerLeft">
            <img className="logo" src={logo}></img>
            <div>
              <h1>WORKOUT</h1>
              <h1>BUDDY</h1>
            </div>
          </div>
        </div>
        <div className="MainSection">
            <div className="formContainer">
                
                <form onSubmit={handleSubmit}>
                    <div>
                    <div className="formInput">
                        <label htmlFor='name'>Name</label>
                        <input type='text' name='name'></input>
                    </div>
                    <div className="formInput">
                        <label htmlFor='email'>Email</label>
                        <input type='text' name='email'></input>
                    </div>
                    <div className="formInput">
                        <label htmlFor='password'>Password</label>
                        <input type='password' name='password'  onMouseLeave={handleMouseLeave} onMouseOver={handleMouseOver}></input>
                    </div>
                        <div className="formInput">
                            <label htmlFor='passwordRepeat'>Repeat password</label>
                            <input type='password' name='passwordRepeat'></input>
                        </div>
                    </div>
                    <button className="registerLoginButtons" type='submit'>REGISTER</button>
                    <button className="registerLoginButtons"><Link to='/'>RETURN</Link></button>
                    <div id="popUp">
                        <ul>
                        Password must:
                        <li>be at least 8 characters long</li>
                        <li>contain one number</li>
                        <li>contain one letter</li>
                        <li>contain one special character</li>
                        </ul>
                    </div>
                </form>
            </div>
        </div>
      </div>
    );
  }